'use client';

import Link from 'next/link';
import { ShoppingCart, Heart, User, Search, LogOut } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { useCartStore } from '@/lib/cart-store';
import { useWishlistStore } from '@/lib/wishlist-store';
import { useAuthStore } from '@/lib/auth-store';
import { useState } from 'react';
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from '@/components/ui/dropdown-menu';
import { useRouter } from 'next/navigation';

export function Header() {
  const { getItemCount: getCartCount } = useCartStore();
  const { getItemCount: getWishlistCount } = useWishlistStore();
  const { user, isAuthenticated, logout } = useAuthStore();
  const router = useRouter();
  const [searchQuery, setSearchQuery] = useState('');

  const cartCount = getCartCount();
  const wishlistCount = getWishlistCount();

  const handleLogout = () => {
    logout();
    router.push('/');
  };

  const handleSearch = (e: React.FormEvent) => {
    e.preventDefault();
    if (searchQuery.trim()) {
      router.push(`/shop?search=${encodeURIComponent(searchQuery)}`);
    }
  };

  return (
    <header className="bg-white border-b border-border sticky top-0 z-50">
      <div className="bg-primary text-primary-foreground text-sm py-2">
        <div className="max-w-7xl mx-auto px-4 flex justify-between items-center">
          <div className="flex gap-6 text-xs">
            <span>MY ACCOUNT</span>
            <span>SHOP</span>
            <span>WELCOME TO Focus India Online</span>
          </div>
          <div className="flex gap-4 text-xs">
            <span>+919959594444</span>
            <span>CONTACT US</span>
            <span>FAQs</span>
          </div>
        </div>
      </div>

      {/* Main header content */}
      <div className="max-w-7xl mx-auto px-4 py-4">
        <div className="flex items-center justify-between gap-4 mb-4">
          {/* Logo */}
          <Link href="/" className="font-bold text-2xl text-primary flex-shrink-0">
            <div className="text-center">
              <div className="text-sm font-bold">TBS</div>
              <div className="text-xs">Focus India Online</div>
            </div>
          </Link>

          {/* Search bar */}
          <form onSubmit={handleSearch} className="flex-1 max-w-md">
            <div className="relative">
              <Input
                type="text"
                placeholder="Search for products"
                className="pr-10"
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
              />
              <button type="submit" className="absolute right-3 top-3">
                <Search className="w-5 h-5 text-muted-foreground" />
              </button>
            </div>
          </form>

          {/* Right actions */}
          <div className="flex items-center gap-4">
            {/* User Menu */}
            {isAuthenticated && user ? (
              <DropdownMenu>
                <DropdownMenuTrigger asChild>
                  <Button variant="ghost" size="icon" title="Account">
                    <User className="w-5 h-5" />
                  </Button>
                </DropdownMenuTrigger>
                <DropdownMenuContent align="end">
                  <div className="px-2 py-1.5 text-sm font-semibold">
                    {user.displayName}
                  </div>
                  <div className="px-2 text-xs text-muted-foreground pb-2">
                    {user.email}
                  </div>
                  <DropdownMenuSeparator />
                  <DropdownMenuItem asChild>
                    <Link href="/account">My Account</Link>
                  </DropdownMenuItem>
                  <DropdownMenuItem asChild>
                    <Link href="/account/orders">My Orders</Link>
                  </DropdownMenuItem>
                  <DropdownMenuItem asChild>
                    <Link href="/account/profile">Profile Settings</Link>
                  </DropdownMenuItem>
                  <DropdownMenuSeparator />
                  <DropdownMenuItem onClick={handleLogout}>
                    <LogOut className="w-4 h-4 mr-2" />
                    Logout
                  </DropdownMenuItem>
                </DropdownMenuContent>
              </DropdownMenu>
            ) : (
              <Button variant="ghost" size="icon" asChild title="Login">
                <Link href="/login">
                  <User className="w-5 h-5" />
                </Link>
              </Button>
            )}

            {/* Wishlist */}
            <Button variant="ghost" size="icon" asChild title="Wishlist" className="relative">
              <Link href="/wishlist">
                <Heart className="w-5 h-5" />
                {wishlistCount > 0 && (
                  <span className="absolute -top-2 -right-2 bg-accent text-accent-foreground text-xs rounded-full w-5 h-5 flex items-center justify-center font-bold">
                    {wishlistCount}
                  </span>
                )}
              </Link>
            </Button>

            {/* Cart */}
            <Button variant="ghost" size="icon" asChild title="Cart" className="relative">
              <Link href="/cart">
                <ShoppingCart className="w-5 h-5" />
                {cartCount > 0 && (
                  <span className="absolute -top-2 -right-2 bg-accent text-accent-foreground text-xs rounded-full w-5 h-5 flex items-center justify-center font-bold">
                    {cartCount}
                  </span>
                )}
              </Link>
            </Button>
          </div>
        </div>

        {/* Category navigation */}
        <nav className="bg-primary text-primary-foreground rounded-md overflow-x-auto">
          <div className="flex gap-2 px-4 py-3 text-sm font-medium whitespace-nowrap">
            <Link href="/" className="hover:bg-primary-foreground/10 px-3 py-1 rounded">HOME</Link>
            <Link href="/shop/APPSC" className="hover:bg-primary-foreground/10 px-3 py-1 rounded">APPSC</Link>
            <Link href="/shop/TGPSC" className="hover:bg-primary-foreground/10 px-3 py-1 rounded">TGPSC</Link>
            <Link href="/shop/UPSC" className="hover:bg-primary-foreground/10 px-3 py-1 rounded">UPSC</Link>
            <Link href="/shop/SSC" className="hover:bg-primary-foreground/10 px-3 py-1 rounded">SSC</Link>
            <Link href="/shop/RRB" className="hover:bg-primary-foreground/10 px-3 py-1 rounded">RRB</Link>
            <Link href="/shop/BANKING" className="hover:bg-primary-foreground/10 px-3 py-1 rounded">BANKING</Link>
            <Link href="/shop/MAGAZINES" className="hover:bg-primary-foreground/10 px-3 py-1 rounded">MAGAZINES</Link>
            <Link href="/shop" className="hover:bg-primary-foreground/10 px-3 py-1 rounded">SHOP ALL</Link>
          </div>
        </nav>
      </div>
    </header>
  );
}
