'use client';

import Link from 'next/link';
import Image from 'next/image';
import { Card } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Heart, ShoppingCart, Star } from 'lucide-react';
import { useCartStore } from '@/lib/cart-store';
import { useWishlistStore } from '@/lib/wishlist-store';
import { useState } from 'react';

interface ProductCardProps {
  id: string;
  title: string;
  author: string;
  image: string;
  price: number;
  originalPrice?: number;
  rating?: number;
  discount?: number;
  slug: string;
}

export function ProductCard({
  id,
  title,
  author,
  image,
  price,
  originalPrice,
  rating = 4.5,
  discount,
  slug
}: ProductCardProps) {
  const { addItem: addToCart } = useCartStore();
  const { addItem: addToWishlist, removeItem: removeFromWishlist, isInWishlist } = useWishlistStore();
  const [isInWish, setIsInWish] = useState(isInWishlist(id));

  const handleAddToCart = (e: React.MouseEvent) => {
    e.preventDefault();
    addToCart({
      bookId: id,
      title,
      author,
      price,
      originalPrice,
      image,
      quantity: 1,
      slug,
    });
    console.log("[v0] Added to cart:", title);
  };

  const handleToggleWishlist = (e: React.MouseEvent) => {
    e.preventDefault();
    if (isInWish) {
      removeFromWishlist(id);
      setIsInWish(false);
      console.log("[v0] Removed from wishlist:", title);
    } else {
      addToWishlist({
        bookId: id,
        title,
        author,
        price,
        image,
        slug,
        addedAt: new Date(),
      });
      setIsInWish(true);
      console.log("[v0] Added to wishlist:", title);
    }
  };

  return (
    <Link href={`/product/${slug}`}>
      <Card className="overflow-hidden hover:shadow-lg transition-all h-full flex flex-col group cursor-pointer">
        {/* Image Container */}
        <div className="relative bg-secondary overflow-hidden h-48 flex items-center justify-center">
          <Image
            src={image || "/placeholder.svg"}
            alt={title}
            width={200}
            height={280}
            className="object-contain group-hover:scale-105 transition-transform"
          />
          {discount && (
            <div className="absolute top-2 left-2 bg-accent text-accent-foreground text-xs font-bold px-2 py-1 rounded">
              -{discount}%
            </div>
          )}
        </div>

        {/* Content */}
        <div className="p-4 flex-1 flex flex-col">
          <h3 className="font-semibold text-sm line-clamp-2 text-balance">{title}</h3>
          <p className="text-xs text-muted-foreground mb-3">{author}</p>

          {/* Rating */}
          {rating && (
            <div className="flex items-center gap-1 mb-3">
              <div className="flex">
                {[...Array(5)].map((_, i) => (
                  <Star
                    key={i}
                    className={`w-3 h-3 ${
                      i < Math.floor(rating)
                        ? 'fill-accent text-accent'
                        : 'text-muted'
                    }`}
                  />
                ))}
              </div>
              <span className="text-xs text-muted-foreground">({rating})</span>
            </div>
          )}

          {/* Price */}
          <div className="mb-4 flex-1">
            <div className="flex items-baseline gap-2">
              <span className="font-bold text-lg text-primary">₹{price}</span>
              {originalPrice && (
                <span className="text-xs line-through text-muted-foreground">
                  ₹{originalPrice}
                </span>
              )}
            </div>
          </div>

          {/* Actions */}
          <div className="flex gap-2 mt-auto">
            <Button
              size="sm"
              className="flex-1"
              onClick={handleAddToCart}
            >
              <ShoppingCart className="w-4 h-4 mr-2" />
              Add
            </Button>
            <Button
              size="sm"
              variant="outline"
              onClick={handleToggleWishlist}
              className={isInWish ? 'text-accent' : ''}
            >
              <Heart className={`w-4 h-4 ${isInWish ? 'fill-accent' : ''}`} />
            </Button>
          </div>
        </div>
      </Card>
    </Link>
  );
}
